.App {
  text-align: center;
  background-color: #1d1f21;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 100%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  margin: 5px;
}

.App-intro {
  font-size: large;
}

.App-links{
  color: #CCC;
  font-family: sans-serif;
  margin-bottom: 10px;
}

.App-links a{
  color: #CCC;
  margin: 0 5px 0 5px;
}
.App-links a:hover{
  color: #42B4F5;
}

@-webkit-keyframes App-logo-spin {
  0% { -webkit-filter: blur(0px); filter: blur(0px) }
  25% { -webkit-filter: blur(3px); filter: blur(3px) }
  50% { -webkit-filter: blur(0px); filter: blur(0px) }
  75% { -webkit-filter: blur(5px); filter: blur(5px) }
  100% { -webkit-filter: blur(0px); filter: blur(0px) }
}

@keyframes App-logo-spin {
  0% { -webkit-filter: blur(0px); filter: blur(0px) }
  25% { -webkit-filter: blur(3px); filter: blur(3px) }
  50% { -webkit-filter: blur(0px); filter: blur(0px) }
  75% { -webkit-filter: blur(5px); filter: blur(5px) }
  100% { -webkit-filter: blur(0px); filter: blur(0px) }
}

.Content{
  max-width: 850px;
  width: 90%;
  margin: auto;
}

.disable-scroll{
  overflow: hidden;
}

::-webkit-scrollbar{
  width: 10px;
  height: 10px;
  background-color: #666;
}

::-webkit-scrollbar-thumb{
  background-color: #999;
  -webkit-transition: background-color 0.5s ease-in-out;
  -o-transition: background-color 0.5s ease-in-out;
  transition: background-color 0.5s ease-in-out;
}

::-webkit-scrollbar-thumb:hover{
  background-color: #FF716E;
}

.cpy-txt{
  font-family: sans-serif;
  color: #CCC;
  padding-bottom: 20px;
  padding-top: 5px;
  background-color: #161718;
}



