.LightBox{
    position:fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 10;
    color: white;
}

.lbox-bg{
    background-color: #000000D0;
    position: absolute;
    width: 100%;
    height: 100%;
}

.lbox-cnt{
    position:absolute;
    width: 100%;
    height: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    vertical-align:center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
}

.lbox-wrap{
    height: 100%;
    position: relative;
    top: 50%;
    -webkit-transform: perspective(1px) translateY(-50%);
            transform: perspective(1px) translateY(-50%);
    -ms-flex: 1 1;
        flex: 1 1;
}

.lbox-img{
    max-width: 100%;
    max-height: 100%;
    position: relative;
    top: 50%;
    -webkit-transform: perspective(1px) translateY(-50%);
            transform: perspective(1px) translateY(-50%);
    cursor: pointer;
}

.lbox-desc{
    text-align: left;
}

.lbox-btn{
    color: white;
    line-height: 100%;
    text-align: center;
    font-size: 2em;
    height: 100%;
    cursor: pointer;
    width: 30px;
    -webkit-transition: background-color 0.3s ease-in-out;
    -o-transition: background-color 0.3s ease-in-out;
    transition: background-color 0.3s ease-in-out;
}

.lbox-btnl{
    background-color: #42B4F500;
    border-left: 2px solid #42B4F5;
}

.lbox-btnl:hover{
    background-color: #42B4F5;
}

.lbox-btnr{
    background-color: #FF716E00;
    border-right: 2px solid #FF716E;
}

.lbox-btnr:hover{
    background-color: #FF716E;
}


