#gallery{
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;

}

.blur{
    -webkit-filter: blur(3px);
            filter: blur(3px);
}

.gallery-col{
    -ms-flex: 1 1;
        flex: 1 1;
    margin: 10px;
    padding: 0px;
}

.gallery-img{
    max-width: 100%;
    border: 0px solid #eeeeee;
    margin: 0 0 20px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: outline-width ease-in-out 0.1s;
    -o-transition: outline-width ease-in-out 0.1s;
    transition: outline-width ease-in-out 0.1s;
    outline: 0px solid #42B4F5;
    cursor: pointer;
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.gallery-img-p{
    outline-color: #FF716E;
}

.gallery-img-b{
    outline-color: #42B4F5;
}

.gallery-img:hover{
    outline-width: 4px;
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
}


